import {
  Column,
  CreateDateColumn,
  Entity,
  ManyToOne,
  PrimaryGeneratedColumn,
} from "typeorm";
import type { User } from "./User";
import type { Room } from "./Room";

@Entity()
export class GameStats {
  @PrimaryGeneratedColumn()
  id: number;

  // @ts-ignore
  @ManyToOne("User", (user) => user.gameStats)
  user: User;

  @Column("integer")
  userId: number;

  @ManyToOne("Room")
  room: Room;

  @Column("integer")
  roomId: number;

  @Column("integer")
  correctGuesses: number;

  @Column("integer")
  incorrectGuesses: number;

  @Column("boolean", { default: false })
  isWinner: boolean;

  @Column("float")
  eloAtGame: number;

  @Column("integer", { default: 0 })
  winStreak: number;

  @CreateDateColumn()
  createdAt: Date;

  // Store coordinates and letters of correct guesses
  @Column("simple-json", { nullable: true })
  correctGuessDetails: {
    row: number;
    col: number;
    letter: string;
    timestamp: Date;
  }[];
}
